import React, { useState, useEffect, useContext, useRef } from 'react'
import { useDrop } from 'react-use';
const myContext = React.createContext()

const Child = () => {
  const value = useContext(myContext);
  return (
    <div>
      <myContext.Consumer>{(val) => { return <h1>{val}</h1> }}</myContext.Consumer>
      <hr />
      <h2 style={{ color: 'red' }}>{value}</h2>
    </div>
  )
}
// 第三方库
const Demo = () => {
  const state = useDrop({
    onFiles: files => console.log('files', files),
    onUri: uri => console.log('uri', uri),
    onText: text => console.log('text', text),
  });
  return (
    <div style={{ width: '500px', height: '600px', background: 'red' }}>
      Drop something on the page.
    </div>
  );
};
// 自定义hooks
const useCounts = () => {
  const [count, setCount] = useState(0)
  useEffect(() => {
    let Id = setInterval(() => {
      setCount((c) => ++c)
    }, 1000)
    return () => { clearInterval(Id) }
  }, [])
  return { count, setCount }
}
export default function Hook() {
  const [count, setCount] = useState(0)
  const [list, setList] = useState([{ id: 0, name: '小明' }])
  const ulRef = useRef(10)
  console.log(ulRef)
  useEffect(() => {
    console.log('go')
    return () => {
      console.log('componentWillUnmount')
    }
  })
  useEffect(() => {
    console.log('componentDidMount')

  }, [])
  useEffect(() => {
    console.log('hh')
  }, [list])


  return (
    <myContext.Provider value={count}>
      <p>{count}</p>
      <button onClick={() => { setCount((old) => ++old) }}>add</button>
      <ul ref={ulRef}>
        {list.map(item => <li key={item.id}>{item.name}</li>)}
      </ul>
      <button onClick={() => {
        setList((old) => {
          let newList = [{ id: old.length, name: '小黄' }, ...old]
          return newList
        })
      }} >list</button>

      <hr />
      <Child />
      <hr />
      <div>
        <h1 style={{ color: 'lime' }} >{useCounts().count}</h1>
      </div>
      <hr />
      <Demo />


    </myContext.Provider>
  )
}
